import androidx.compose.animation.core.animateFloatAsState
import androidx.compose.foundation.Canvas
import androidx.compose.foundation.layout.*
import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.StrokeCap
import androidx.compose.ui.graphics.drawscope.Stroke
import androidx.compose.ui.unit.dp
import com.skydoves.flexible.core.InternalFlexibleApi
import com.skydoves.flexible.core.toPx
import kotlinx.coroutines.delay

@OptIn(InternalFlexibleApi::class)
@Composable
fun AnimatedArcProgressBar(progress: Float) {
    val arcThickness = 20.dp.toPx() //  -
    val innerColor = Color.Gray //  
    val progressColor = Color.White //  

    Canvas(modifier = Modifier.size(300.dp)) {
        //   
        drawArc(
            color = innerColor,
            startAngle = 0f, //   0 
            sweepAngle = 180f, //  180 
            useCenter = false,
            style = Stroke(width = arcThickness, cap = StrokeCap.Round) //  
        )

        //   
        drawArc(
            color = progressColor,
            startAngle = -180f, //   190 
            sweepAngle = -180f * progress, //     
            useCenter = false,
            style = Stroke(width = arcThickness, cap = StrokeCap.Round) //  
        )
    }
}


@Composable
fun ProgressBarDemo() {
    var progress by remember { mutableStateOf(0f) }
    val animatedProgress by animateFloatAsState(targetValue = progress)

    LaunchedEffect(Unit) {
        while (progress < 1f) {
            delay(100) //   
            progress += 0.01f //  
        }
    }

    Box(
        modifier = Modifier.fillMaxWidth(),
        contentAlignment = Alignment.BottomCenter //  - 
    ) {
        AnimatedArcProgressBar(animatedProgress)
    }
}
